$prefix: 'bx' !default;

@import "@carbon/colors/scss/colors";
@import "@carbon/layout/scss/layout";
@import "@carbon/themes/scss/themes";
@import '@carbon/type/scss/styles';
// Grid-specific import
// See https://github.com/IBM/carbon-elements/blob/master/packages/grid/README.md
// @import '@carbon/grid/scss/grid';
// Grid-specific layout
// See https://github.com/IBM/carbon-elements/blob/master/packages/layout/README.md
// @import '@carbon/layout/scss/layout';

@import "@carbon/type/scss/_reset.scss";
@include carbon--type-reset();

// Faccio il reset dei css
@import '@carbon/type/scss/font-face/sans';
@include carbon--font-face-sans();
$font-family: 'IBM Plex Sans', 'Helvetica Neue', Arial, sans-serif;

// @include carbon--font-face-serif();
// @include carbon--font-face-mono();
@import '@carbon/type/scss/classes';
@include carbon--type-classes();

//---------------------------------------
// Set Theme
// See https://github.com/IBM/carbon-elements/blob/master/packages/themes/README.md
//---------------------------------------

// Set the Dark Theme
$carbon--theme: $carbon--theme--g90;
// Use the gray 100 theme
@include carbon--theme();



// Abilito le feature experiemental
$feature-flags: (
  ui-shell: true,
  components-x: true,
  grid-columns-16: true,
);

//---------------------------------------
// Import all component
//---------------------------------------
// @import 'carbon-components/scss/globals/scss/styles';


//---------------------------------------
// Import Specific component
//---------------------------------------
// $css--plex: true !default;
// $font-family: 'IBM Plex Sans', 'Helvetica Neue', Arial, sans-serif;
@import '@carbon/import-once/scss/import-once';
@import 'carbon-components/scss/components/button/button';
// @import 'carbon-components/scss/components/checkbox/checkbox';


html {
  // font-size: calc(0.4em + 1vmin);
  height:100%;
}

body {
  background: $ui-background;
  font-family: $font-family;
  overflow: hidden;
  color: $text-01;
  height:100%;
  display: flex;
  flex: 1;
  flex-direction: column;
  display: flex;
  justify-content: center;
}

#app{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 40%;
    width: 50%;
    align-self: center;
    border: 3px solid $carbon--blue-50;
}

